export class Host {
    constructor(window, platform, commands) {
        this.window = window;
        this.platform = platform;
        this.commands = commands;
        this.commands$ = this.commands.asObservable();
    }
    appendDomElement(el) {
        this.window.document.body.appendChild(el);
    }
    createCSSStyleSheet() {
        return new CSSStyleSheet();
    }
    addCSSStyleSheet(style) {
        if (this.window.document.adoptedStyleSheets.find(x => x === style))
            return;
        this.window.document.adoptedStyleSheets.push(style);
    }
    removeCSSStyleSheet(style) {
        this.window.document.adoptedStyleSheets = this.window.document.adoptedStyleSheets.filter(x => x !== style);
    }
    registerCommand(command_name, callback, options = {}) {
        this.commands.next([...this.commands.getValue(), { name: command_name, exec: callback }]);
    }
}
export class Platform {
    constructor(_events) {
        this._events = _events;
        this._services = new Map();
        this.window = window;
        this.events$ = _events.asObservable();
    }
    setHost(host) {
        this.host = host;
    }
    static getInstance() {
        return window.platform;
    }
    register(serviceName, service) {
        this._services.set(serviceName, service);
    }
    getService(serviceName) {
        return this._services.get(serviceName);
    }
}
